import { toLower } from "lodash";

/*
- **Rle** : Ce fichier dfinit la classe `Country`, reprsentant le modle de donnes et la prsentation d'un pays individuel.
- **Fonctionnalits** :
    - Contient des donnes spcifiques au pays, telles que le drapeau et les rponses valides (drives des noms de pays dans diffrentes langues).
    - Fournit une mthode pour vrifier si une rponse donne est correcte.
    - Gre l'affichage du drapeau du pays dans l'interface utilisateur.

    ### Proprits

1. Une proprit contenant les donnes d**un** pays. Ces donnes doivent correspondre  un objet du tableau retourn par lAPI. Cet objet sera pass  chaque instance de Country par le `constructor`.
2. Une proprit contenant toutes les rponses possibles pour un pays (ce tableau sera construit dans une mthode dcrite plus loin). 
3. Une proprit gnre par un retournant uniquement le drapeau du pays.

*/
class Country {

    constructor(data) {
        this.flag = data.flags.png;
        this.name = data.name.common;
        this.answers = this.answers(data.translations);
    
    }

    answers(translations) {
        let answers = new Set();
        for (let key in translations) {
            answers.add(toLower(translations[key].common));
        }
        return answers;
    }
   
    
    

    getFlag() {
        return this.flag;
    }
    
    showFlag() {
        document.getElementById('flag').innerHTML = '';
        let flag = document.createElement('img');
        flag.src = this.flag;
        flag.alt = `Flag of ${this.name}`;
        document.getElementById('flag').appendChild(flag);
    }

    //test if an answer is true
    isCorrect(answer) {
        return this.answers.has(toLower(answer));
    }

    

}

export default Country;